import ISuiteNavLink from './ISuiteNavLink';
import ISuiteNavOverrideStrings from '../ISuiteNavOverrideStrings';

interface ISuiteNavData {
  /** App switcher links */
  WorkloadLinks: Array<ISuiteNavLink>;
  /** Settings menu links */
  CurrentWorkloadSettingsSubLinks: Array<ISuiteNavLink>;
  /** Help menu links */
  CurrentWorkloadHelpSubLinks?: Array<ISuiteNavLink>;
  /** User/me menu links. Required if authenticated. */
  CurrentWorkloadUserSubLinks?: Array<ISuiteNavLink>;
  /** Sign out link (irrelevant if not authenticated) */
  SignOutLink?: ISuiteNavLink;
  /**
   * Should be an app switcher link ID from SuiteNavLinkIds.
   * It's actually required, but it's optional in this interface because it should
   * always be SuiteNavLinkIds.ONEDRIVE (so implementations can just set it to that).
   */
  CurrentMainLinkElementID?: string;

  /**
   * Required string with additional data. Implementors can use a SuiteNavClientData object
   * as a constructor parameter or to store this data.
   */
  ClientData?: string;
  /** Whether user is authenticated */
  IsAuthenticated?: boolean;
  /** User's name. Required if authenticated. */
  UserDisplayName?: string;
  /** Override strings */
  StringsOverride?: ISuiteNavOverrideStrings;

  // More properties are present in the actual data blob, especially for business.
  // Feel free to add them here as appropriate.
  // To see all possible properties, go to regular ODB and in the debugger:
  // JSON.parse(localStorage.SPSuiteLinksJson)
  AboutMeLink?: ISuiteNavLink;
  CommunityLink?: ISuiteNavLink;
  CompanyDisplayName?: string;
  CorrelationID?: string;
  CultureName?: string;
  Dimensions?: { Top: string };
  FeedbackLink?: ISuiteNavLink;
  FlightName?: string;
  FlipHelpIcon?: boolean;
  HelpLink?: ISuiteNavLink;
  LegalLink?: ISuiteNavLink;
  LogoNavigationUrl?: string;
  MyProfileUrl?: string;
  O365SettingsLink?: ISuiteNavLink;
  PrivacyLink?: ISuiteNavLink;
  SessionID?: string;
}

export default ISuiteNavData;
